<template>
  <div class="container mt-5">
    <h1>Person List</h1>
    <ul class="list-group">
      <li
        class="list-group-item d-flex justify-content-between align-items-center"
        v-for="person in persons"
        :key="person.id"
      >
        {{ person.first_name }} {{ person.last_name }} -
        {{ person.phone_number }} - {{ person.age }}
        <div>
          <button class="btn btn-warning btn-sm" @click="editPerson(person)">
            Edit
          </button>
          <button
            class="btn btn-danger btn-sm"
            @click="deletePerson(person.id)"
          >
            Delete
          </button>
        </div>
      </li>
    </ul>
    <div v-if="selectedPerson" class="mt-5">
      <h2>Edit Person</h2>
      <form @submit.prevent="updatePerson">
        <div class="mb-3">
          <label for="first_name" class="form-label">First Name:</label>
          <input
            type="text"
            class="form-control"
            v-model="selectedPerson.first_name"
            required
          />
        </div>
        <div class="mb-3">
          <label for="last_name" class="form-label">Last Name:</label>
          <input
            type="text"
            class="form-control"
            v-model="selectedPerson.last_name"
            required
          />
        </div>
        <div class="mb-3">
          <label for="phone_number" class="form-label">Phone Number:</label>
          <input
            type="text"
            class="form-control"
            v-model="selectedPerson.phone_number"
            required
          />
        </div>
        <div class="mb-3">
          <label for="age" class="form-label">Age:</label>
          <input
            type="number"
            class="form-control"
            v-model="selectedPerson.age"
            required
          />
        </div>
        <button type="submit" class="btn btn-primary">Update</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      persons: [],
      selectedPerson: null,
    };
  },
  created() {
    this.fetchPersons();
  },
  methods: {
    async fetchPersons() {
      try {
        const response = await axios.get("http://127.0.0.1:8000/api/persons/");
        this.persons = response.data;
      } catch (error) {
        console.error("There was an error fetching persons!", error);
      }
    },
    editPerson(person) {
      this.selectedPerson = { ...person };
    },
    async updatePerson() {
      try {
        await axios.put(
          `http://127.0.0.1:8000/api/persons/${this.selectedPerson.id}/`,
          this.selectedPerson
        );
        alert("Person updated successfully");
        this.selectedPerson = null;
        this.fetchPersons();
      } catch (error) {
        console.error("There was an error updating the person!", error);
      }
    },
    async deletePerson(personId) {
      try {
        await axios.delete(`http://127.0.0.1:8000/api/persons/${personId}/`);
        alert("Person deleted successfully");
        this.fetchPersons();
      } catch (error) {
        console.error("There was an error deleting the person!", error);
      }
    },
  },
};
</script>

