<template>
  <div class="container mt-5">
    <h1>Person Form</h1>
    <form @submit.prevent="submitForm">
      <div class="mb-3">
        <label for="first_name" class="form-label">First Name:</label>
        <input
          type="text"
          class="form-control"
          v-model="person.first_name"
          required
        />
      </div>
      <div class="mb-3">
        <label for="last_name" class="form-label">Last Name:</label>
        <input
          type="text"
          class="form-control"
          v-model="person.last_name"
          required
        />
      </div>
      <div class="mb-3">
        <label for="phone_number" class="form-label">Phone Number:</label>
        <input
          type="text"
          class="form-control"
          v-model="person.phone_number"
          required
        />
      </div>
      <div class="mb-3">
        <label for="age" class="form-label">Age:</label>
        <input
          type="number"
          class="form-control"
          v-model="person.age"
          required
        />
      </div>
      <button type="submit" class="btn btn-primary">Submit</button>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      person: {
        first_name: "",
        last_name: "",
        phone_number: "",
        age: null,
      },
    };
  },
  methods: {
    async submitForm() {
      try {
        await axios.post("http://127.0.0.1:8000/api/persons/", this.person);
        this.$emit("personAdded"); // Emit an event when a person is added
        this.person = {
          first_name: "",
          last_name: "",
          phone_number: "",
          age: null,
        };
      } catch (error) {
        console.error("There was an error!", error);
      }
    },
  },
};
</script>
